html {
  scroll-snap-type:  y mandatory;
}

.App {
  background: #000;
  height: 100vh;
  display: grid;
  place-items: center;
}

.app__videos {
  height: 100vh;
  max-height: 800px;
  width: 80%;
  max-width: 400px;
  border-radius: 20px;
  overflow: scroll;
  scroll-snap-type: y mandatory;
  position: relative;
}

/* Esconde a scrollbar no chrome, safari e opera*/

.app__videos::-webkit-scrollbar {
  display: none;
}


/* Esconde a scrollbar no internet explorer, edge e firebox*/


.app__videos {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

@media only screen and (max-width: 440px){
  .app__videos{
    width: 100%;
    height: 100vh;
  }

}